<template>
  <h1 v-if="level === 1">{{ text }}</h1>
  <h2 v-else-if="level === 2">{{ text }}</h2>
</template>

<script>
export default {
  name: 'Heading',
  props: {
    text: {
      type: String,
      required: true
    },
    level: {
      type: Number,
      default: 1
    }
  }
}
</script>

