<template>
  <section class="problem-video">
    <video
      poster="../assets/section-problem-video/graph.png"
      :src="videoSrc"
      muted
      preload
      loop
      v-autoplay-visible.once
    />
  </section>
</template>

<script>
import AutoplayVisible from '../directives/AutoplayVisible'

export default {
  name: 'ProblemVideo',
  created () {
    this.videoSrc = require('../assets/section-problem-video/graph.mp4')
  },
  beforeMount () {
    if (window.innerWidth > 1440) {
      this.videoSrc = require('../assets/section-problem-video/graph-feathered.mp4')
    }
  },
  directives: {
    AutoplayVisible
  }
}
</script>

<style lang="scss" scoped>
$breakpoint-md: 800px;

.problem-video {
  margin: 0 auto 2rem;
  max-width: 1440px;

  @media (min-width: $breakpoint-md) {
    margin-bottom: 0;
  }
}

video {
  width: 100%;
}
</style>
