        :key="team.slug"
        :href="`#${team.slug}`"
        :class="{ active: team.slug === activeTeam.slug }"
        @click.prevent="activeTab = index"
        v-text="team.name"
      />
    </nav>

    <div class="members">
      <div>
        <slider
          :id="activeTeam.slug"
          :key="activeTeam.slug"
          :per-page="3"
          gutter="4rem"
        >
          <div
            class="member"
            v-for="member in activeTeam.members"
            :key="member.name"
          >
            <img
              class="avatar"
              :src="member.avatar"
              :srcset="`${member.avatar2x} 2x, ${member.avatar} 1x`"
            />
            <h3>{{ member.name }}</h3>
            <subheading
              class="subheading"
              :text="member.role"
            />
            <div
              class="content"
              v-html="member.content"
            />
          </div>
        </slider>
      </div>
    </div>
  </section>
</template>

<script>
import Subheading from '../components/Subheading'
import Slider from '../components/Slider'

export default {
  name: 'TeamMembers',
  props: {
    content: Object
  },
  data () {
    return {
      activeTab: 0
    }
  },
  computed: {
    activeTeam () {
      return this.content.teams[this.activeTab]
    }
  },
  created () {
    this.sliderConfig = {
      items: 3,
      slideBy: 1,
      // controls: false,
      nav: false,
      loop: false,
      mouseDrag: true
    }
  },
  components: {
    Subheading,
    Slider
  }
}
</script>

