<template>
  <div class="toggle-field">
    <input
      :id="uniqueId"
      type="checkbox"
      :checked="checked"
      @change="onChange"
    />

    <label :for="uniqueId" />
  </div>
</template>

<script>
export default {
  name: 'ToggleField',
  model: {
    prop: 'checked',
    event: 'change'
  },
  props: {
    checked: {
      type: Boolean,
      required: true
    }
  },
  methods: {
    onChange (e) {
      this.$emit('change', e.target.checked)
    }
  },
  created () {
    this.uniqueId = `switch_${this._uid}`
  }
}
</script>

<style lang="scss" scoped>
.toggle-field {
  position: relative;
}

input {
  position: absolute;
  opacity: 0;
}

label {
  position: relative;
  display: block;
  width: 2.5rem;
  height: 1rem;
  background-color: rgba(#DE7A7A, .25);
  border-radius: .5rem;
  box-sizing: border-box;
  cursor: pointer;

  &::after {
    position: absolute;
    top: calc(#{-(1.5rem - 1rem) / 2} - 1px);
    left: -1px;
    width: 1.5rem;
    height: 1.5rem;
    background-color: #DE7A7A;
    border-radius: 50%;
    transform: translateX(0px);
    transition: transform .2s ease-out;
    content: '';
  }
}

input:checked + label::after {
  transform: translateX(2.5rem - 1.5rem);
}
</style>
