function onVisible (intersections, observer) {
  let el = this
  let threshold = observer.thresholds[0]
  let { isIntersecting, intersectionRatio } = intersections[0]

  if (isIntersecting && intersectionRatio >= threshold) {
    el.play()
  }
}

function onVisibleOnce (intersections, observer) {
  let threshold = observer.thresholds[0]
  let { isIntersecting, intersectionRatio, target } = intersections[0]

  if (isIntersecting && intersectionRatio >= threshold) {
    observer.unobserve(target)
  }

  onVisible.call(this, intersections, observer)
}

function initFallback (el) {
  el.autoplay = true

  if (el.readyState === el.HAVE_ENOUGH_DATA) {
    el.play()
  }
}

function initObserver (el, threshold, once) {
  if (!window.IntersectionObserver) {
    return initFallback(el)
  }

  let callback = (once ? onVisibleOnce : onVisible)
  let observer = new window.IntersectionObserver(callback.bind(el), {
    threshold: (threshold || 0)
  })

  observer.observe(el)

  return observer
}

export default {
  bind (el, binding) {
    el.observer = initObserver(el, binding.value, binding.modifiers.once)
  },
  update (el, binding) {
    el.observer.disconnect()
    el.observer = initObserver(el, binding.value, binding.modifiers.once)
  },
  unbind (el) {
    el.observer.disconnect()
    delete el.observer
  }
}
