<template>
  <section class="intro-video">
    <video
      poster="../assets/section-intro-video/money.jpg"
      :src="videoSrc"
      muted
      preload
      loop
      v-autoplay-visible.once=".5"
    />
  </section>
</template>

<script>
import AutoplayVisible from '../directives/AutoplayVisible'

export default {
  name: 'IntroVideo',
  created () {
    this.videoSrc = require('../assets/section-intro-video/money.mp4')
  },
  beforeMount () {
    if (window.innerWidth > 1440) {
      this.videoSrc = require('../assets/section-intro-video/money-feather.mp4')
    }
  },
  directives: {
    AutoplayVisible
  }
}
</script>

<style lang="scss" scoped>
.intro-video {
  margin: 0 auto;
  max-width: 1440px;
}

video {
  width: 100%;
}
</style>
