<template>
  <div class="video-modal" v-if="active">
    <button class="btn-close" @click="$emit('close')">
      <img src="../assets/icons/icon-close.svg">
    </button>

    <iframe src="https://player.vimeo.com/video/291617237" frameborder="0" webkitallowfullscreen mozallowfullscreen allowfullscreen></iframe>
  </div>
</template>

<script>
export default {
  name: 'VideoModal',
  props: {
    active: Boolean
  },
  methods: {
    onKeyUp (e) {
      if (e.key === 'Escape') {
        this.$emit('close')
      }
    }
  },
  beforeMount () {
    this.bindOnKeyUp = this.onKeyUp.bind(this)
    document.addEventListener('keyup', this.bindOnKeyUp)
  },
  beforeDestroy () {
    document.removeEventListener('keyup', this.bindOnKeyUp)
  }
}
</script>

