async function fetchDraggable () {
  await import(/* webpackChunkName: 'gsap' */ 'gsap/ThrowPropsPlugin')
  return (await import(/* webpackChunkName: 'gsap' */ 'gsap/Draggable')).default
}

async function initDraggable (el) {
  const Draggable = await fetchDraggable()
  let childOffsets = [...el.children].map(child => -child.offsetLeft)
  let endOffset = (el.parentNode.clientWidth - el.clientWidth)

  return new Draggable(el, {
    type: 'x',
    bounds: el.parentNode,
    throwProps: true,
    snap: [...childOffsets, endOffset],
    zIndexBoost: false,
    cursor: 'grab',
    onPress () {
      el.style.cursor = 'grabbing'
    },
    onRelease () {
      el.style.cursor = 'grab'
    }
  })
}

export default {
  bind (el) {
    el.style.position = 'relative'
  },
  async inserted (el) {
    el._draggable = await initDraggable(el)
  },
  unbind (el) {
    el.style.position = ''

    if (el._draggable) {
      el._draggable.kill()
    }
  }
}
