<template>
  <div
    class="slider"
    :style="{ width: totalWidth }"
    v-draggable
  >
    <slot />
  </div>
</template>

<script>
import Draggable from '../directives/Draggable'

export default {
  name: 'Slider',
  props: {
    perPage: {
      type: Number,
      default: 1
    },
    gutter: {
      type: String,
      default: '0px'
    }
  },
  computed: {
    totalWidth () {
      let totalItems = this.$slots.default.length
      let totalPages = (totalItems / this.perPage)

      return `calc(${(totalPages * 100)}% + (${(totalPages - 1)} * ${this.gutter}))`
    }
  },
  directives: {
    Draggable
  }
}
</script>

<style lang="scss" scoped>
.slider {
  display: flex;
}

.slider > * {
  flex: 1 1 100%;
}
</style>
